.footer-container {
    width: 100%;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(58, 55, 55);
    color: white;
    position: relative; /* To allow absolute positioning of the text */
}

.footer-sections {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.footer-logo {
    width: 33%;
    text-align: left;
}

.footer-contacts {
    margin: 0 20px;
    width: 33%;
    text-align: left;
}

.footer-schools-list {
    margin: 0 20px;
    width: 33%;
    text-align: left;
}

@media (max-width: 768px) {
    .footer-container {
        width: 100%;;
        flex-direction: column; /* Stack sections vertically on smaller screens */
        height: auto; /* Allow height to adjust based on content */
    }
    .footer-sections {
        flex-direction: column; /* Stack sections vertically on smaller screens */
    }
    
    .footer-contacts, 
    .footer-schools-list {
        margin: 10px 0; /* Add vertical spacing between sections */
    }

    .footer-logo,
    .footer-contacts,
    .footer-schools-list {
        width: 100%; /* Make each section take full width on smaller screens */
        text-align: center; /* Center text for better readability */    
    }
}